import React, {Component} from 'react';
import './Menu.css';
import {Link} from 'react-router-dom';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import QuienesS from'./QuienesSomos.js';
import Footer from'./footer.js';
import QS from'./QS.js';
import Noticias from'./Noticias.js';
import galery from'./galery.js';
import Principal from'./ImagenPrincipal.js';
import Estatutos from'./Estatutos.js';
import Estatutos2 from'./Estatutos2.js';
import Contacto from'./Contacto.js';





class Welcome extends React.Component {
    render() {
      return (
        <div className="contenedorMenu">
       
         <nav><ul>
         <BrowserRouter>
        <li><Link to="/" className="link">Inicio</Link></li>
        <li><Link to="/QuienesS" className="link">Quienes Somos</Link></li>
        <li><Link to="/galery" className="link">Galeria</Link></li>
           <li><Link>Estatutos</Link>
            <ul><li>  <Link to="/Estatutos" className="link">Quiroga I sector</Link></li>
            <li>  <Link to="/Estatutos2" className="link">Ley 743 de 2002</Link></li>
            
            </ul>
            </li>
        <li><Link to="/Noticias" className="link">Noticias</Link></li>
        <li><Link to="/Contacto">Contacto</Link></li>
        
        

        <Switch>
       
       <Route path='/footer' component={Footer}></Route>
       <Route path='/galery' component={galery}></Route>
       <Route path='/Estatutos' component={Estatutos}></Route>
       <Route path='/Estatutos2' component={Estatutos2}></Route>
       <Route path='/QuienesS' component={QS}></Route>
       <Route path='/Noticias' component={Noticias}></Route>
       <Route path='/Contacto' component={Contacto}></Route>
       <Route path='/' component={Principal}></Route>
     
       </Switch>
        </BrowserRouter>
    </ul></nav>
       
        
     
        </div>
      );
    }
  }
  
export default Welcome;